.scroll_content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.scroll_content>div {
    height: 100%;
    align-items: center;
}

.scroll_content>div:hover {
    color: rgba(219, 101, 5, 0.801);
    /* color: rgba(199, 46, 0, 0.904); */
    border-bottom: 2px solid rgba(255, 115, 0, 0.801);
}
.box_shadow {
    /* box-shadow: rgba(33, 35, 38, 0.1) 0px 10px 10px -10px; */
    box-shadow: 10px 10px 19px rgb(0 0 0 / 10%);
    position: sticky;
    top: 0;
    z-index: 5;
    background-color: #ECF0F3;
    /* background-image: linear-gradient(to bottom, #171A26, rgba(23, 26, 38, 0.0001)); */
}